	const login = require('../../static/utils/utils').Login;
	const query = require('../../static/utils/utils').Query;
	const Login = new login();
	const Query = new query();
	
	import {format,formatDate} from'../../static/utils/time.js';
	import {dateWorkAdd,
	        taskUpdateBatch,
			dateWorkQuery,
			dateWorkUpdateBatch
	       } from "../../static/utils/api.js";
	var _this;
	export default {
		data() {
			return {
			   width:"",                  //设置输入框和输入区域的长度
			   userInfo:"",
			   projectId:"",                             //这4个也传
			   actualWorkingHours:"",                    //目前的实际工时
			   sprintId:"",
			   departmentId:"",
			   taskId:"",                                  //任务的id.
			   taskOrder:"",                            //任务的序号 
			   taskName:"",            //任务的内容  这三个参数都通过页面传输参数传过来。
			   
               taskStatusArry:["未开始","完成","中止","进行中"],
			   taskDisplayStatus:"未完成",      //默认的任务状态
			   
			   
			   taskState:0,          // 0:未完成 1:完成 2:中止 3:进行中
			   dayWorkTime:"",        //输入的每天工时
			   preDayWorkTime:"",            //打过卡原有的当天的工时
			   dayNote:"",            //每天的日报
			   completionDegree:""         ,//每日的完成度
			   
			   dateWorkId:"",                //dateWorkId。用于更新用
			   
			}
		},
		onShow(){
			_this = this;
			_this.getSystem();
			uni.getStorage({
				key:"userInfo",
				success:(res)=>{
					let id = {
						id:res.data.id
					}
				  Query.findUser(id)
				  .then(data=>{     
					  console.log("查询到的用户信息",data.data.records);
					  _this.userInfo = data.data.records[0];
					  _this.getHadNowDateWork();             //当任务已经存在的时候，需要修改的时候显示原来有的。
				  })
				  .catch(Error=>{
					  uni.showToast({
					  	title:'网络错误',
						duration:5000,
						icon:"none"
					  })
				  })
				},
				fail:()=>{
					uni.redirectTo({
						url:'../login/login'
					})
				}
			})
		},
		
		onLoad(e){
			_this = this;
			let item = JSON.parse(e.item);
			_this.projectId = parseInt(item.projectId);
			_this.sprintId = parseInt(item.sprintId);
			_this.departmentId = parseInt(item.departmentId);
			_this.taskId = parseInt(item.taskId);
			_this.taskOrder = item.taskOrder;
			_this.taskName = item.taskName;
			_this.actualWorkingHours = parseFloat(item.actualWorkingHours);
		},
		
		methods: {
			
			//获取系统信息设置输入框的长度
			getSystem:function(){
				_this = this;
				uni.getSystemInfo({
					success:(res)=>{
						_this.width = parseInt(res.windowWidth-70)
					}
				})
			},
		   
		    //查询今日有没有打卡，有的就显示，再次提交的时候就更新
			getHadNowDateWork:function(){
				_this = this;
			    let dateTime;                                  //打卡的时间,填入的时间段
			    let nowHours = new Date().getHours();
			    let nowTimeStap = new Date().getTime();           //当前的时间磋
			    if(nowHours>=0&&nowHours<12){                     //在3点之前就查前一天的
			        nowTimeStap -=1*12*60*60*1000;
			    	let year = new Date(nowTimeStap).getYear();
			    	let month = new Date(nowTimeStap).getMonth()+1;
			    	let day = new Date(nowTimeStap).getDate()
			    	dateTime = year+'-'+month+'-'+day;
			    }else{
			        dateTime = format(new Date());
			    }
			    uni.showLoading({
			    	title:"获取中",
				    success:()=>{
						uni.request({
							url:dateWorkQuery,
							method:"POST",
							data:{
								taskId:_this.taskId,
								userId:_this.userId,
								sprintId:_this.sprintId,
								projectId:_this.projectId,
								date:dateTime,
							},
							dataType:'json'
						})
						.then(data=>{
							uni.hideLoading();
							console.log("查找成功",data[1].data.data.records);
						    if(data[1].data.data.records.length){  //查找是否存在
								_this.dateWorkId = data[1].data.data.records[0].id;
								_this.dayWorkTime = data[1].data.data.records[0].workTime;
								_this.preDayWorkTime = data[1].data.data.records[0].workTime;
								_this.dayNote = data[1].data.data.records[0].note;
								_this.taskState = 3;
							}else{
								
							}
						})
						.catch(Error=>{
							uni.showToast({
								title:"网络错误",
								icon:"loading",
								duration:500
							})
						})
					}
			    })
			},
		    
		   
		    pickerChange:function(e){
				_this = this;
				let index = e.detail.value;
				_this.taskDisplayStatus = _this.taskStatusArry[index];
				_this.taskState = index;
			},			
			
			inputDayWokrTime:function(e){
				_this = this;
				let dayWorkTime = e.detail.value;
			    _this.dayWorkTime = dayWorkTime;
				// let reg = /^[0-9]+$/g;
				// if(reg.test(dayWorkTime)){
				// 	_this.dayWorkTime = dayWorkTime;
				// }else{
				// 	uni.showToast({
				// 		title:"请输入数字",
				// 		icon:"none",
				// 	    duration:500,
				// 		success:()=>{
				// 			_this.dayWorkTime = "";
				// 		}
				// 	})
				// }
			},
			
			inputDayNote:function(e){
				let dayNote = e.detail.value;
				_this.dayNote = dayNote;
			},
			
		    sliderChange:function(e){
				_this = this;
				_this.completionDegree = e.detail.value+'%';
			},
			
			
			//提交每日打卡, 之前已经存在的就进行更新。
			submitDateWork:function(){
				_this = this;
				if(_this.dayNote&&_this.dayWorkTime
				   &&_this.completionDegree&&_this.taskId
				   ){
					let dateTime;                               //打卡的时间,填入的时间段
					let nowHours = new Date().getHours();
					let nowTimeStap = new Date().getTime();         //当前的时间磋
					if(nowHours>=0&&nowHours<12){                    //在12点之前记录到前一天
                        nowTimeStap -=1*12*60*60*1000;
						let year = new Date(nowTimeStap).getYear();
						let month = new Date(nowTimeStap).getMonth()+1;
						let day = new Date(nowTimeStap).getDate()
						dateTime = year+'-'+month+'-'+day;
					}else{
					    dateTime = format(new Date());
					}
							  
					if(_this.dateWorkId){              //已经存在则进行更新
						_this.updateDateWork()
					}else{                              //不存在进行时间的判断，3点之前的日报都记录到前一天
						
					    uni.showLoading({
					    	title:"提交中",
					    	success:()=>{
					    		uni.request({
					    			url:dateWorkAdd,
					    			method:"POST",
					    			data:{
					    			  taskId:_this.taskId,
					    		      userId:_this.userInfo.id,
					    			  sprintId:_this.sprintId,
					    			  projectId:_this.projectId,
					    			  date:dateTime,
					    			  completionDegree:_this.completionDegree,
					    			  note:_this.dayNote,
					    			  workTime:_this.dayWorkTime
					    			},
					    			dataType:'json'
					    		})
					    		.then(data=>{
					    			console.log('提交成功',data)
					    			if(_this.taskState){  //当选中的状态不为未完成时，更新任务的状态
					    			   _this.updatetTask();
					    			}else{
											uni.showToast({
											title:"打卡成功",
											icon:"../../static/img/Icon/success.png",
											duration:500,
											})
									}
					    		})
					    		.catch(Error=>{
					    			uni.showToast({
					    				title:"网络错误",
					    				icon:"loading",
					    				duration:500
					    			})
					    		})
					    	}
					    })	
					}
				}else{
					uni.showToast({
						title:"完善信息或中止请填0",
						duration:500,
						icon:"none"
					})
				}
			},
			
			
			//当已经存在dateWorkId的时候进行更新
			updateDateWork:function(){
				_this = this;
				uni.showLoading({
					title:"更新中",
					success:()=>{
						uni.request({
							url:dateWorkUpdateBatch,
							method:"POST",
							data:[{
								"id":_this.dateWorkId,
					            "completionDegree": _this.completionDegree,
					            "note": _this.dayNote,
					            "workTime": _this.dayWorkTime
					          }],
							dataType:'json',
						})
						.then(data=>{
							console.log("更新打卡",data)
							_this.updatetTask()
						})
						.catch(Error=>{
							uni.showToast({
								title:"网络错误",
								icon:"loading",
								duration:1000
							})
						})
					}
				})

			},
			
			//当taskStatus选中为非0状态的时候向任务表中更新状态和实际工时
			updatetTask:function(){
				_this = this;
				let submitActualWorkingHours = 0;
				if(_this.dateWorkId){      //同一天打卡的时候
					submitActualWorkingHours = parseFloat(_this.actualWorkingHours)-parseFloat(_this.preDayWorkTime)+parseFloat(_this.dayWorkTime);
				}else{
					submitActualWorkingHours = parseFloat(_this.actualWorkingHours)+parseFloat(_this.dayWorkTime);
				}
				uni.showLoading({
					title:"更新中",
					success:()=>{
						uni.request({
							url:taskUpdateBatch,
							method:"POST",
							data:[{
								id:_this.taskId,
								taskState:_this.taskState,
								actualWorkingHours:submitActualWorkingHours
							}],
							dataType:'json'
						})
						.then(data=>{
							console.log("更新任务成功",data)
							uni.showToast({
								title:"更新成功",
								icon:"../../static/img/Icon/success.png",
								duration:500,
								success:()=>{
									uni.navigateBack({
										delta:1
									})
								}
							})
						})
						.catch(Error=>{
							uni.showToast({
								title:"网络错误",
								icon:"loading",
								duration:1000
							})
						})
					}
				})
			},
			
		}
	}
